@font-face {
    font-family: 'MuseoSansCyrl_1';
    src: url('../fonts/MuseoSansCyrl_1.eot');
    src: url('../fonts/MuseoSansCyrl_1.eot') format('embedded-opentype'),
        url('../fonts/MuseoSansCyrl_1.woff2') format('woff2'),
        url('../fonts/MuseoSansCyrl_1.woff') format('woff'),
        url('../fonts/MuseoSansCyrl_1.ttf') format('truetype'),
        url('../fonts/MuseoSansCyrl_1.svg#MuseoSansCyrl_1') format('svg');
}

@font-face {
    font-family: 'MuseoSansCyrl100';
    src: url('../fonts/MuseoSansCyrl100.eot');
    src: url('../fonts/MuseoSansCyrl100.eot') format('embedded-opentype'),
        url('../fonts/MuseoSansCyrl100.woff2') format('woff2'),
        url('../fonts/MuseoSansCyrl100.woff') format('woff'),
        url('../fonts/MuseoSansCyrl100.ttf') format('truetype'),
        url('../fonts/MuseoSansCyrl100.svg#MuseoSansCyrl100') format('svg');
}

.f500 {
    font-family: 'MuseoSansCyrl_1';
}
