@import 'smart-grid.less';
@import 'fonts.less';
@import 'index.less';
.reset();
.container {
    .container();
}
html {
    font-size: 100%;
    .xxlg-block({
        font-size: 50%;
    });
    .sm-block({
        font-size: 40%;
    });
}
a {
    text-decoration: none;
    color: inherit;
}
a:hover {
    text-decoration: none;
}
body {
    font-family: 'MuseoSansCyrl100', 'Helvetica', sans-serif;

}
ul,li {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}
input {
    border: none;
}
button {
    background: none;
    border: none;
}

.loader {
    background: none repeat scroll 0 0 #ffffff;
    bottom: 0;
    left: 0;
    height: 100%;
    position: fixed;
    right: 0;
    top: 0;
    width: 100%;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
}
.btn_mnu {
    width: 50px;
    height: 50px;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    cursor: pointer;
    position: relative;
    left: 50px;
    opacity: 0;
    .md-block({
        opacity: 1;
    });
}
.btn_row {
    height: 2px;
    width: 60%;
    margin: 2px 0;
    background-color: #fff;
    position: relative;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    z-index: 101;
}
.btn_mnu.active .btn_row:first-child {
    -webkit-transform: rotate(-135deg);
    -ms-transform: rotate(-135deg);
    -o-transform: rotate(-135deg);
    transform: rotate(-135deg);
    top: 6px;
}
.btn_mnu.active .btn_row:last-child {
    -webkit-transform: rotate(-225deg);
    -ms-transform: rotate(-225deg);
    -o-transform: rotate(-225deg);
    transform: rotate(-225deg);
    top: -6px;
}
.btn_mnu.active .btn_row:nth-child(2) {
    opacity: 0;
}