.f500 {
  font-family: 'MuseoSansCyrl_1';
}

.wrapper {
  background-color: #000;
  color: #fff;
  width: 100%;
  height: 100%;
}

.main {
  padding-left: 20px;
  padding-right: 8.6rem;
}

.header_nav--item.mactive {
  border-bottom: 3px solid #45494f;
}

.header_nav.left {
  left: 0;
}

.btn_row.black {
  background-color: #000;
}

.header {
  top: 0px;
  padding-top: 5.3rem;
}

.header_wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
  align-items: center;
}

.header_left {
  display: flex;
  align-items: center;
}

.header_nav {
  left: 8rem;
}

.header_nav--item {
  display: inline-block;
  color: #fffefe;
  font-size: 1.73rem;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 1.56px;
  margin: 0px 3.3rem;
  padding: 2.6rem 0;
}

.header_nav--item:hover {
  border-bottom: 3px solid #45494f;
}

.header_logo {
  position: relative;
  left: 20px;
}

.header_mail {
  color: #fffefe;
  font-size: 2rem;
  position: relative;
  right: 8rem;
}

.section {
  margin-top: 3.3rem;
  margin-left: 14.13rem;
}

.first_blue {
  display: inline-block;
  padding: 19px 22px;
  background-color: #205bcf;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.73rem;
  line-height: 4.5rem;
  text-transform: uppercase;
  letter-spacing: 2.08px;
}

.first_h1 {
  margin: 10rem 0 4rem;
  font-size: 8.4rem;
  line-height: 7.06rem;
}

.first_descr {
  color: rgba(255, 255, 255, 0.4);
  font-size: 2.6rem;
  line-height: 4.26rem;
  max-width: 85.3rem;
}

.first_btn {
  display: inline-block;
  margin-top: 4rem;
  padding: 2.6rem 0;
  font-size: 1.86rem;
  line-height: 3.6rem;
  text-transform: uppercase;
  letter-spacing: 2.27px;
  border-bottom: 3px solid #45494f;
  transition: 1s all;
}

.first_btn:hover {
  border-bottom: none;
  border-top: 3px solid #45494f;
}

.second_blue {
  display: inline-block;
  padding: 19px 22px;
  background-color: #205bcf;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.73rem;
  line-height: 4.5rem;
  text-transform: uppercase;
  letter-spacing: 2.08px;
}

.second_wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
  margin-top: 10rem;
}

.second_item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(29.166666666666668% - 30px);
}

.second_item--h3 {
  margin: 6.3rem 0 4.3rem;
  font-size: 2.26rem;
  line-height: 4rem;
  text-transform: uppercase;
}

.second_item--p {
  font-size: 2.26rem;
  line-height: 3.9rem;
  color: rgba(255, 255, 255, 0.3);
  max-width: 34.6rem;
}

.third_title {
  font-size: 8.4rem;
  line-height: 7.06rem;
}

.third_form {
  margin-top: 8.6rem;
}

.third_input,
.third_textarea {
  color: #fff;
  font-size: 2.13rem;
  line-height: 4.2rem;
  background: transparent;
  border: none;
  border-bottom: 1px solid #6f7070;
}

.third_input::placeholder,
.third_textarea::placeholder {
  font-size: 2.13rem;
  line-height: 4.2rem;
  color: rgba(255, 255, 255, 0.3);
}

.third_input {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(25% - 30px);
  padding: 2.67rem 0;
}

.third_textarea {
  margin-top: 1.67rem;
  padding-top: 2.67rem;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
}

.third_btn {
  cursor: pointer;
  display: block;
  margin-top: 6.6rem;
  padding: 2.67rem 0;
  font-size: 1.86rem;
  line-height: 3.6rem;
  text-transform: uppercase;
  letter-spacing: 2.27px;
  border-bottom: 3px solid #45494f;
  transition: 1s all;
  color: #fff;
}

.third_btn:hover {
  border-bottom: none;
  border-top: 3px solid #45494f;
}

.footer_social {
  position: relative;
  opacity: 0.3;
  font-size: 2.13rem;
  line-height: 4.26rem;
  padding-left: 4.53rem;
  transition: .3s all;
}

.footer_social:hover {
  opacity: 1;
}

.footer_drib {
  margin-left: 4.67rem;
}

.footer_drib::after {
  content: '';
  background: url(../img/dribbble%20logo.png) no-repeat;
  background-size: cover;
  background-position: center center;
  width: 46px;
  height: 46px;
  position: absolute;
  top: 0;
  left: 0;
}

.footer_inst::after {
  content: '';
  background: url(../img/instagram%20logo.png) no-repeat;
  background-size: cover;
  background-position: center center;
  position: absolute;
  top: 0;
  left: 0;
}

.header,
.footer {
  position: fixed;
  width: 100%;
  z-index: 9;
  background-color: #000;
}

.footer {
  bottom: 0px;
  padding-left: 20px;
