.wrapper {
    background-color: #000;
    color: #fff;
    width: 100%;
    height: 100%;
}
.main {
    padding-left: 20px;
    padding-right: 8.6rem;
}
.header_nav--item.mactive {
    border-bottom: 3px solid #45494f;
}
.header_nav.left {
    left:0;
}
.btn_row.black {
    background-color: #000;
}
.header {
    top:0px;
    padding-top: 5.3rem;
    .xlg-block({
        padding-top: 2rem;
    });
    &_wrap {
        .row-flex();
        justify-content: space-between;
        align-items: center;
    }
    &_left {
        display: flex;
        align-items: center;
    }
    &_nav {
        left: 8rem;
        .md-block({
            position: fixed;
            height: 100vh;
            width: 50vw;
            background: #fff;
            z-index: 100;
            top: 0;
            left: -500px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            transition: 1s all;
        });
        &--item {
            display: inline-block;
            color: #fffefe;
            font-size: 1.73rem;
            line-height: 24px;
            text-transform: uppercase;
            letter-spacing: 1.56px;
            margin: 0px 3.3rem;
            padding: 2.6rem 0;
            &:hover {
                border-bottom: 3px solid #45494f;
            }
            .xlg-block({
               padding: 2rem 0;
            });
            .md-block({
                position: relative;
                text-align: left;
                color: #000;
            });
        }
    }
    &_logo {
        position: relative;
        left: 20px;
        img {
            .xlg-block({
                width: 54px;
                height: 62px;
            });
            .md-block({
                width: 37px;
                height: 43px;
            });
            .xs-block({
                display: none;
            });
        }
    }
    &_mail {
        color: #fffefe;
        font-size: 2rem;
        position: relative;
        right: 8rem;
    }
}
.section {
    margin-top: 3.3rem;
    margin-left: 14.13rem;
    .xlg-block({
       margin-top: 0rem;
    });
    .md-block({
        margin-left: 9rem;
    });
    .sm-block({
        margin-left: 20px;
    });
}
.first {
    &_blue {
        display: inline-block;
        padding: 19px 22px;
        background-color: #205bcf;
        color: rgba(255,255,255,0.7);
        font-size: 1.73rem;
        line-height: 4.5rem;
        text-transform: uppercase;
        letter-spacing: 2.08px;
        .xlg-block({
            font-size: 1.5rem;
            line-height: 3rem;
            padding: 12px 15px;
        });
        .md-block({
            font-size: 1.2rem;
            line-height: 2rem; 
        });
        .mob-block({
            font-size: 12px;
            line-height: 1.3;
            padding: 8px 12px;
        });
    }
    &_h1 {
        margin: 10rem 0 4rem;
        font-size: 8.4rem;
        line-height: 7.06rem;
        .xlg-block({
            margin: 7rem 0 3rem;
            font-size: 7rem;
            line-height: 6.5rem;
        });
        .md-block({
            margin: 5rem 0 3rem;
            font-size: 5rem;
            line-height: 3rem;
        });
        .mob-block({
            margin: 25px 0 10px;
            font-size: 28px;
            line-height: 1.3;
        });
    }
    &_descr {
        color: rgba(255,255,255,0.4);
        font-size: 2.6rem;
        line-height: 4.26rem;
        max-width: 85.3rem;
        .xlg-block({
            font-size: 2.4rem;
            line-height: 3.5rem;
        });
        .md-block({
            font-size: 2rem;
            line-height: 3rem;
            max-width: 650px;
        });
        .sm-block({
            max-width: 450px;
        });
        .mob-block({
            font-size: 14px;
            line-height: 1.3;
        });
        
    }
    &_btn {
        display: inline-block;
        margin-top: 4rem;
        padding: 2.6rem 0;
        font-size: 1.86rem;
        line-height: 3.6rem;
        text-transform: uppercase;
        letter-spacing: 2.27px;
        border-bottom: 3px solid #45494f;
        transition: 1s all;
        .xlg-block({
            margin-top: 2rem;
        });
        .md-block({
            margin-top: 3rem;
            padding: 2rem 0;
            font-size: 1.5rem;
            line-height: 3rem;
        });
        .mob-block({
            font-size: 12px;
            line-height: 1.3;
        });
        &:hover {
            border-bottom: none;
            border-top: 3px solid #45494f;
        }
    }
}
.second {
    &_blue {
        display: inline-block;
        padding: 19px 22px;
        background-color: #205bcf;
        color: rgba(255,255,255,0.7);
        font-size: 1.73rem;
        line-height: 4.5rem;
        text-transform: uppercase;
        letter-spacing: 2.08px;
        .xlg-block({
            font-size: 1.5rem;
            line-height: 3rem;
            padding: 12px 15px;
        });
        .md-block({
            font-size: 1.2rem;
            line-height: 2rem; 
        });
        .mob-block({
            display: none;
            font-size: 12px;
            line-height: 1.3;
            padding: 8px 12px;
        });
    }
    &_wrap {
        .row-flex();
        justify-content: space-between;
        margin-top: 10rem;
        .xlg-block({
            margin-top: 7rem;
        });
        .md-block({
            margin-top: 5rem;
        });
        .mob-block({
            margin-top: 0;
            justify-content: center;
        });
    }
    &_item {
        .col();
        .size(3.5);
        .size-xlg(4);
        .size-mob(8);
        &--h3 {
            margin: 6.3rem 0 4.3rem;
            font-size: 2.26rem;
            line-height: 4rem;
            text-transform: uppercase;
            .xlg-block({
                margin: 2rem 0 1.5rem;
                font-size: 2rem;
                line-height: 3rem;
            });
            .md-block({
                margin: 2rem 0 1.5rem;
                font-size: 1.8rem;
                line-height: 3rem;
            });
            .mob-block({
                font-size: 14px;
                line-height: 1.5;
            });
        }
        &--p {
            font-size: 2.26rem;
            line-height: 3.9rem;
            color: rgba(255,255,255,0.3);
            max-width: 34.6rem;
            .xlg-block({
                font-size: 2rem;
                line-height: 3.2rem;
            });
            .md-block({
                font-size: 1.8rem;
                line-height: 3rem;
                max-width: 220px;
            });
            .mob-block({
                font-size: 14px;
                line-height: 1.3;
            });
        }
        &--img {
            .xlg-block({
                width: 81px;
                height: 81px;
            });
            .mob-block({
                padding-top: 10px;
                width: 40px;
                height: 40px;
            });
        }
    }
}
.third {
    &_title {
        font-size: 8.4rem;
        line-height: 7.06rem;
        .xlg-block({
            font-size: 7rem;
            line-height: 6.5rem;
        });
        .md-block({
            font-size: 5rem;
            line-height: 3rem;
        });
        .mob-block({
            font-size: 28px;
            line-height: 1.3;
        });
    }
    &_form {
        margin-top: 8.6rem;
        .xlg-block({
            margin-top: 4rem;
        });
        .mob-block({
            margin-top: 15px;
        });
    }
    &_input,
    &_textarea {
        color: #fff;
        font-size: 2.13rem;
        line-height: 4.2rem;
        background: transparent;
        border: none;
        border-bottom: 1px solid #6f7070;  
        &::placeholder {
            font-size: 2.13rem;
            line-height: 4.2rem;
            color: rgba(255,255,255,0.3);
            .xlg-block({
                font-size: 2rem;
                line-height: 3rem;
            });
            .mob-block({
                font-size: 14px;
                line-height: 1.3;
            });
        }
        .xlg-block({
            font-size: 2rem;
            line-height: 3rem;
        });
        .md-block({
            font-size: 1.8rem;
            line-height: 2.5rem;
        });
        .mob-block({
            font-size: 14px;
            line-height: 1.3;
        });
    }
    &_input {
        .col();
        .size(3);
        .size-mob(8);
        padding: 2.67rem 0;
        .xlg-block({
            padding: 2rem 0;
        });
        .mob-block({
            padding: 15px 0;
        });
        
    }
    &_textarea {
        margin-top: 1.67rem;
        padding-top: 2.67rem;
        .xlg-block({
            padding-top: 2rem;
        });
        .col();
        .size(6);
        .size-mob(8);
    }
    &_btn {
        cursor: pointer;
        display: block;
        margin-top: 6.6rem;
        padding: 2.67rem 0;
        font-size: 1.86rem;
        line-height: 3.6rem;
        text-transform: uppercase;
        letter-spacing: 2.27px;
        border-bottom: 3px solid #45494f;
        transition: 1s all;
        color: #fff;
        .xlg-block({
            margin-top: 3rem;
        });
        .md-block({
            padding: 2rem 0;
            font-size: 1.5rem;
            line-height: 3rem;
        });
        .mob-block({
            margin-top: 15px;
            font-size: 12px;
            line-height: 1.3;
        });
        &:hover {
            border-bottom: none;
            border-top: 3px solid #45494f;
        }
    }
}
.footer {
    &_social {
        position: relative;
        opacity: 0.3;
        font-size: 2.13rem;
        line-height: 4.26rem;
        padding-left: 4.53rem;
        transition: .3s all;
        .xlg-block({
            padding-left: 4.53rem;
            padding-top: 5px;
            font-size: 2rem;
            line-height: 3rem;
        });
        &:hover {
            opacity: 1;
        }
    }
    &_drib {
        margin-left: 4.67rem;
        .xlg-block({
            margin-left: 4rem;
        });
        &::after {
            content: '';
            background: url(../img/dribbble%20logo.png) no-repeat;
            background-size: cover;
            background-position: center center;
            width: 46px;
            height: 46px;
            position: absolute;
            top: 0; left: 0;
            .xlg-block({
                width: 32px;
                height: 32px;
            });
            
        }
    }
    &_inst {
        &::after {
            content: '';
            background: url(../img/instagram%20logo.png) no-repeat;
            background-size: cover;
            background-position: center center;
            .xlg-block({
                width: 32px;
                height: 32px;
            });
            position: absolute;
            top: 0; left: 0;
        }
    }

}
.header, .footer{
    position:fixed;
    width: 100%;
    z-index:9;
    background-color: #000;
}
.footer{
    bottom:0px;
    padding-left: 20px;
    padding-bottom: 25px;
    .mob-block({
        padding-bottom: 10px;
    });
}
#fp-nav ul li a span {
    background-color: #f0f0f0;
}